import { render } from 'react-dom';
import React from 'react';

const appNode = document.getElementById('container');
/**
 * routes - реакт-компонент, но с компонентом реакт-роутер не работает,
 * поэтому приходится делать именно вызов функции, чтобы вернуть завёрнутые роуты.
 */
const app = (
  <div className="app">
    <div className="app-content">
      <div className="loginpage">
        <div className="wrap">
          <form>
            <div className="form-signin">
              <div className="ets-header">ЕТС</div>
              <div className="form-content" style={{ display: 'flex', flexDirection: 'column' }}>
                <label>Сайт находится в разработке</label>
                <a style={{ marginBottom: 20, textDecoration: 'underline' }} href="http://ets.tech.mos.ru/ets-study">Перейти в рабочее приложение</a>
                <div className="tp-messange">
                  <span>Служба технической поддержки</span>
                  <a href="mailto:ETS_support@mos.ru">ETS_support@mos.ru</a>
                  <a href="tel:84951501193">8(495) 150-11-93</ a>
                </div>
                <hr />
                <div className="dit-logo" />
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
);

render(app, appNode);

